"use client";

import React, { useState } from "react";
import LoginForm from "../components/Auth/LoginForm";
import Dashboard from "../components/Dashboard/Dashboard";

export default function HomePage() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  // For demonstration, toggle login state on successful login
  // In real app, use auth context or state management
  const handleLoginSuccess = () => {
    setIsLoggedIn(true);
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center bg-white text-black p-4 space-y-8">
      {!isLoggedIn ? (
        <>
          <h1 className="text-3xl font-bold mb-6">Welcome to the Audio Annotation App</h1>
          <p className="max-w-md text-center mb-4">
            This app allows you to record audio, label segments with predefined tags, and sync your work with the cloud.
          </p>
          <LoginForm onLoginSuccess={handleLoginSuccess} />
        </>
      ) : (
        <Dashboard />
      )}
    </main>
  );
}
