"use client";

import React, { useState } from "react";
import AudioRecorder from "../AudioRecorder/AudioRecorder";
import AudioFileList from "../AudioFileList/AudioFileList";
import Spectrogram from "../Spectrogram/Spectrogram";

interface AudioFile {
  id: string;
  name: string;
  duration: number; // in seconds
  uploadedAt: string;
}

export default function Dashboard() {
  // Placeholder user state, in real app this would come from auth context or similar
  const [user] = useState({ email: "test@example.com" });
  const [audioFiles, setAudioFiles] = useState<AudioFile[]>([
    {
      id: "1",
      name: "Sample Audio 1",
      duration: 125,
      uploadedAt: "2023-06-01T12:00:00Z",
    },
    {
      id: "2",
      name: "Sample Audio 2",
      duration: 240,
      uploadedAt: "2023-06-05T15:30:00Z",
    },
  ]);
  const [selectedFile, setSelectedFile] = React.useState<AudioFile | null>(null);
  const [audioUrl, setAudioUrl] = React.useState<string | null>(null);

  const handleSelectFile = (file: AudioFile) => {
    setSelectedFile(file);
    // TODO: Fetch audio file URL from cloud or local cache
    // For now, simulate with a placeholder URL or null
    setAudioUrl(null);
  };

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h2 className="text-2xl font-bold mb-4">Welcome, {user.email}</h2>
      <section className="mb-8">
        <h3 className="text-xl font-semibold mb-2">Audio Files</h3>
        <AudioFileList files={audioFiles} onSelect={handleSelectFile} />
      </section>
      {selectedFile && (
        <section className="mb-8">
          <h3 className="text-xl font-semibold mb-2">Selected Audio: {selectedFile.name}</h3>
          {audioUrl ? (
            <Spectrogram audioUrl={audioUrl} />
          ) : (
            <p>Spectrogram and labeling UI will be implemented here.</p>
          )}
        </section>
      )}
      <section>
        <h3 className="text-xl font-semibold mb-2">Audio Recording</h3>
        <AudioRecorder />
      </section>
    </div>
  );
}
